import React from 'react';
import Grid from '@mui/material/Grid';
import Checkbox from '@mui/material/Checkbox';
import FormControl from '@mui/material/FormControl';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormGroup from '@mui/material/FormGroup';
import FormLabel from '@mui/material/FormLabel';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import {useFormik} from 'formik';
import {useDispatch, useSelector} from 'react-redux';
import {Navigate} from 'react-router-dom';
import {selectorIsLogin} from './loginSelectors';
import {asyncLoginActions} from './index';
import {LoginParamsType} from '../../api';


type ErrorsType = {
    email?: string | null
    password?: string | null
}

export const Login = () => {
    let dispatch = useDispatch<any>();
    let isLoggedIn = useSelector(selectorIsLogin);

    let {loginTC} = asyncLoginActions

    const formik = useFormik({
        initialValues: {
            email: '',
            password: '',
            rememberMe: false
        },
        validate: (values) => {
            const errors: ErrorsType = {};
            if (!values.email) {
                errors.email = 'Required';
            } else if (
                !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
            ) {
                errors.email = 'Invalid email address';
            }
            if (!values.password) {
                errors.password = 'Required';
            } else if (values.password.length <= 3) {
                errors.password = 'Invalid password, too short  ';
            }
            return errors;
        },

        onSubmit: async (values: LoginParamsType, formikHelpers) => {
            const res = await dispatch(loginTC(values));
            if (loginTC.rejected.match(res)) {
                if (res.payload?.fieldsErrors?.length) {
                    const error=res.payload?.fieldsErrors[0]
                    formikHelpers.setFieldError(error.field, error.error);
                }
                formikHelpers.setFieldError('email', 'fakeError');
            }
        },
    });

    if (isLoggedIn) {
        return <Navigate to={'/'}/>;
    }

    return <Grid container justifyContent={'center'}>
        <Grid item justifyContent={'center'}>
            <form onSubmit={formik.handleSubmit}>
                <FormControl>
                    <FormLabel>
                        <p>To log in get registered
                            <a href={'https://social-network.samuraijs.com/'}
                               target={'_blank'}> here
                            </a>
                        </p>
                        <p>or use common test account credentials:</p>
                        <p>Email: free@samuraijs.com</p>
                        <p>Password: free</p>
                    </FormLabel>
                    <FormGroup>

                        <TextField label="Email" margin="normal" {...formik.getFieldProps('email')}/>
                        {formik.errors.email && <div style={{color: 'red'}}>{formik.errors.email}</div>}

                        <TextField type="password" label="Password"
                                   margin="normal" {...formik.getFieldProps('password')}/>
                        {formik.errors.password && <div style={{color: 'red'}}>{formik.errors.password}</div>}


                        <FormControlLabel label={'Remember me'}
                                          control={<Checkbox/>} {...formik.getFieldProps('rememberMe')}
                                          checked={formik.values.rememberMe}/>
                        <Button type={'submit'} variant={'contained'} color={'primary'}
                                disabled={!!formik.errors.email || !!formik.errors.password}>
                            Login
                        </Button>
                    </FormGroup>
                </FormControl>
            </form>
        </Grid>;
    </Grid>;
};


